import { Box, Button, Chip, Container, InputAdornment, Menu, MenuItem, Stack, TextField, Typography } from '@mui/material';
import { Helmet } from 'react-helmet-async';
import { useSettingsContext } from 'src/components/settings';
import { color, decryptToken, formatDate, formatObj } from 'src/utils/common';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
import { useState, useEffect, useDebugValue } from 'react';
import { constants } from 'src/utils/constant';
import axios, { endpoints } from 'src/utils/axios';
import { useSnackbar } from 'src/components/snackbar';
import DataTable from 'react-data-table-component';
import VisibilityIcon from '@mui/icons-material/Visibility';
import SearchIcon from '@mui/icons-material/Search';
import { useLocation, useNavigate } from 'react-router';
import { paths } from 'src/routes/paths';
import dayjs from 'dayjs';
import { DatePicker, LocalizationProvider } from '@mui/x-date-pickers';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import * as XLSX from 'xlsx';
import JsPDF from 'jspdf';
import 'jspdf-autotable';
import { saveAs } from 'file-saver';
// sections

// ----------------------------------------------------------------------

export default function Ledger() {
    const settings = useSettingsContext();
    const [isLoading, setIsLoading] = useState(false)
    const [list, setList] = useState([]);
    const [tempData, setTempData] = useState([]);
    const [searchQuery, setSearchQuery] = useState('');
    const { enqueueSnackbar } = useSnackbar();
    // For Actions Dropdown
    const [anchorEl, setAnchorEl] = useState(null);
    const openMenu = Boolean(anchorEl);
    const navigate = useNavigate();
    const location = useLocation();

    const fetchSales = async (startDate, endDate) => {

        try {
            const decryptedToken = localStorage.getItem(constants.keyUserToken);
            const accessToken = decryptToken(decryptedToken);
            const response = await axios.post(endpoints.reports.ledger, { startDate, endDate }, {
                headers: {
                    Authorization: `Bearer ${accessToken}`,
                }
            });
            if (response) {
                setIsLoading(false);
                setList(response.data.data);
                setTempData(response.data.data);
            }
        } catch (err) {
            enqueueSnackbar('Failed to fetch ledgers!', { variant: 'error' });
            setIsLoading(false);
        }
    };
    const [startDate, setStartDate] = useState(dayjs().startOf('month'));
    const [endDate, setEndDate] = useState(dayjs().endOf('month'));

    const handleStartDateChange = (date) => {
        setStartDate(date);
    };

    const handleEndDateChange = (date) => {
        setEndDate(date);
    };


    useEffect(() => {
        setIsLoading(true);
        if (startDate && endDate) {
            fetchSales(startDate.toDate(), endDate.toDate());
        }

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [startDate, endDate]);


    const columns = [
        {
            name: 'Account',
            selector: row => row.account,
            sortable: true,
        },
        {
            name: 'Debit',
            selector: row => formatObj.format(row.debit),
            sortable: 'true'
        },
        {
            name: 'Credit',
            selector: row => formatObj.format(row.credit),
            sortable: 'true',
        },
        {
            name: 'Balance',
            selector: row => formatObj.format(row.balance),
            sortable: 'true',
        },
        {
            name: 'Actions',
            cell: row => (
                <Button
                    variant='outlined'
                    onClick={() => {
                        // Navigate to ledger Details page
                        navigate(paths.dashboard.reports.ledgerDetails, { state: row });
                    }}
                    style={{ color: color.actionButton }}
                    startIcon={<VisibilityIcon />}
                >
                    View
                </Button>
            ),
            sortable: 'true'
        },
    ];

    const handleDownloadPDF = () => {
        setAnchorEl(null);
        const doc = new JsPDF();
        doc.text('Ledger Report', 10, 10);

        // Get table data
        // Transform data and create tableData array
        const tableData = filteredData.map(item => [
            item.account,
            item.debit,
            item.credit,
            item.balance,
        ]);

        // Set column headers
        const headers = ['Account', 'Debit', 'Credit', 'Balance'];

        // Add table to PDF
        doc.autoTable({
            head: [headers],
            body: tableData,
        });

        doc.save('ledger_report.pdf');
    };

    const handleDownloadExcel = () => {
        setAnchorEl(null);
        // Convert data to a format suitable for Excel
        const excelData = filteredData.map(item => ({
            'Account': item.account,
            'Debit': item.debit,
            'Credit': item.credit,
            'Balance': item.balance,
        }));

        // Create a new workbook
        const workbook = XLSX.utils.book_new();
        // Convert the data to a worksheet
        const worksheet = XLSX.utils.json_to_sheet(excelData);
        // Add the worksheet to the workbook
        XLSX.utils.book_append_sheet(workbook, worksheet, 'Ledger Report');

        // Convert the workbook to a binary Excel file
        const excelBuffer = XLSX.write(workbook, { bookType: 'xlsx', type: 'array' });
        // Create a Blob from the buffer
        const data = new Blob([excelBuffer], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8' });
        // Save the Blob as a file using file-saver
        saveAs(data, 'ledger_report.xlsx');
    };


    const filteredData = list.filter(data => data?.account?.toLowerCase().includes(searchQuery?.toLowerCase()));

    return (
        <Container maxWidth={settings.themeStretch ? false : 'xl'}>
            <Helmet>
                <title> Reports: Ledger</title>
            </Helmet>

            <Typography variant="h4"> Ledger </Typography>
            <div style={{ display: 'flex', flexDirection: 'row', justifyContent: "flex-end", marginTop: "20px" }}>
                <Stack>
                    <Button style={{ color: color.actionButton }} endIcon={<ArrowDropDownIcon />} variant='outlined' onClick={(e) => setAnchorEl(e.currentTarget)}>
                        ACTIONS
                    </Button>
                    <Menu
                        id="basic-menu"
                        anchorEl={anchorEl}
                        open={openMenu}
                        onClose={() => {
                            console.log("ðŸš€ ~ Ledger ~ endpoints.reports.ledgerDetails:", endpoints.reports.ledgerDetails)
                            setAnchorEl(null);
                            setTempData({});
                        }}
                        MenuListProps={{
                            'aria-labelledby': 'basic-button',
                        }}
                    >

                        <MenuItem onClick={() => {
                        }}>Print</MenuItem>
                        <MenuItem onClick={handleDownloadPDF}>Download as PDF</MenuItem>
                        <MenuItem onClick={handleDownloadExcel}>Download as Excel</MenuItem>

                    </Menu>
                </Stack>
            </div>

            <div style={{ display: "flex", marginTop: "8px", gap: '10px' }}>
                <TextField
                    placeholder='Search'
                    size='small'
                    value={searchQuery}
                    onChange={e => setSearchQuery(e.target.value)}
                    InputProps={{
                        startAdornment: (
                            <InputAdornment position="start">
                                <SearchIcon />
                            </InputAdornment>
                        ),
                    }}
                />
                <div>
                    <LocalizationProvider dateAdapter={AdapterDayjs} >
                        <Box sx={{ display: 'flex', flexDirection: 'row', gap: '10px', alignItems: 'center' }}>
                            <DatePicker
                                label="From"
                                format='DD/MM/YYYY'
                                value={startDate}
                                slotProps={{ textField: { size: 'small' } }}
                                onChange={handleStartDateChange}
                                renderInput={(props) => <TextField {...props} />}
                            />
                            <Typography>-</Typography>
                            <DatePicker
                                label="To"
                                format='DD/MM/YYYY'
                                value={endDate}
                                slotProps={{ textField: { size: 'small' } }}
                                onChange={handleEndDateChange}
                                renderInput={(props) => <TextField {...props} />}
                            />
                        </Box>
                    </LocalizationProvider>
                </div>
            </div>

            <div style={{ marginTop: "20px" }}>
                <DataTable
                    columns={columns}
                    data={filteredData}
                    pagination
                    progressPending={isLoading}
                />
            </div>
        </Container>
    );
}
